<template>
  <!-- -->
  <div class="task-plan-section">
      <h4 @click="section_open===true ? section_open = false : section_open = true">
        
        <i class="icon"
           :class="{'icon-angle-double-up text-danger': section_open===false,'icon-angle-double-down': section_open===true}"></i>
      </h4>
      <blockquote v-show="section_open">
      <div class="form-horizontal tasi-form" role="form">
        <div class="form-group">
          <label class="col-lg-2 control-label">
            <strong></strong>
          </label>
          <div class="col-lg-7" :class="{'has-error': validation.hasError('product_info.url'), 'has-success': validation.isPassed('product_info.url')}">
            <div class="input-group">
              <input v-model="product_info.url" placeholder="http://https://" class="form-control" type="url" @change="getProductInfo">
              <span class="input-group-btn">
                <a type="button" class="btn btn-primary ladda-button loadding-product-info-by-ajax" data-style="expand-right" @click="getProductInfo(true)">
                  <i class="icon-magic"></i>
                  
                </a>
              </span>
            </div>
            <div class="help-block text-danger" v-if="validation.hasError('product_info.url')">
              <i class="icon icon-times-circle color-red"></i>
              {{ validation.firstError('product_info.url') }}
            </div>
          </div>
          <div class="col-lg-10 col-lg-offset-2 clearfix">
            <div class="help-block text-info" v-if="validation.isPassed('product_info.url')">
              <i class="icon icon-info-circle"></i>
              
            </div>
          </div>
        </div>
        <div class="form-group">
          <label class="col-lg-2 control-label">
            <strong></strong>
          </label>
          <div class="col-lg-7" :class="{'has-error': validation.hasError('product_info.title'), 'has-success': validation.isPassed('product_info.title')}">
            <input v-model="product_info.title" placeholder="" class="form-control" type="text">
            <div class="help-block text-danger" v-if="validation.hasError('product_info.title')">
              <i class="icon icon-times-circle color-red"></i>
              {{ validation.firstError('product_info.title') }}
            </div>
          </div>
        </div>
        <div class="form-group">
          <label class="col-lg-2 control-label">
            <strong></strong>
          </label>
          <div class="col-lg-7" :class="{'has-error': validation.hasError('product_info.store_name'), 'has-success': validation.isPassed('product_info.store_name')}">
            <input v-model="product_info.store_name" placeholder="" class="form-control" type="text">
            <div class="help-block text-danger" v-if="validation.hasError('product_info.store_name')">
              <i class="icon icon-times-circle color-red"></i>
              {{ validation.firstError('product_info.store_name') }}
            </div>
          </div>
        </div>

        <!---->
        <ChooseImageForm id="choose_img" :base_data="product_info" title_name=""></ChooseImageForm>
        <!---->

        <div class="form-group" v-if="_inArray('mobile',root.form_data.base_type.flow_type)">
          <label class="col-lg-2 control-label">
            <strong>App</strong>
          </label>
          <div class="col-lg-6" :class="{'has-error': validation.hasError('product_info.price.mobile'), 'has-success': validation.isPassed('product_info.price.mobile')}">
            <div>
              <input v-model="product_info.price.mobile" placeholder="APP" class="form-control per50 inline-block" type="text">
              <span class="mt5 ml5">
               <a class="text-primary" href="/backend/dist/images/tutorial/mobile-price.png" data-lightbox="price.mobile">
                <i class="icon icon-eye text-success"></i>
                <span></span>
              </a>
            </span>
            </div>
            <div class="help-block text-danger" v-if="validation.hasError('product_info.price.mobile')">
              <i class="icon icon-times-circle color-red"></i>
              {{ validation.firstError('product_info.price.mobile') }}
            </div>
          </div>
        </div>
        <div class="form-group" v-if="_inArray('pc',root.form_data.base_type.flow_type)">
          <label class="col-lg-2 control-label">
            <strong></strong>
          </label>
          <div class="col-lg-6" :class="{'has-error': validation.hasError('product_info.price.pc'), 'has-success': validation.isPassed('product_info.price.pc')}">
            <div>
              <input v-model="product_info.price.pc" placeholder="" class="form-control per50 inline-block" type="text">
              <span class="mt5 ml5">
               <a href="/backend/dist/images/tutorial/pc-price.png" data-lightbox="pc-price" class="text-primary">
                <i class="icon icon-eye text-success"></i>
                <span></span>
              </a>
            </span>
            </div>
            <div class="help-block text-danger" v-if="validation.hasError('product_info.price.pc')">
              <i class="icon icon-times-circle color-red"></i>
              {{ validation.firstError('product_info.price.pc') }}
            </div>

          </div>
        </div>
      </div>
    </blockquote>
  </div>
  <!-- -->
</template>

<script>
    import ChooseImageForm from '../form/ChooseImageForm.vue'
    export default {
        components: {
            ChooseImageForm
        },
        data () {
            return {
                section_open: true,
                task_data: this.$parent.$data.task_data,
                root: this.$root,
                product_info: {
                    url: '',
                    title: '',
                    max_img_num: 2,
                    min_img_num: 1,
                    store_name: '',
                    price: {
                        pc: '',
                        mobile: ''
                    },
                    images: [],             //
                    alternative_images: []  //
                },
            }
        },
        created: function () {
            this.$set(this.task_data, 'product_info', this.product_info);
        },
        watch: {
            //
            product_info: {
                handler: function (val, old) {
                    this.$set(this.task_data, 'product_info', val);
                },
                deep: true
            },
            //
            'task_data.product_info': {
                handler: function (val, old) {
                    for (let n in val) {
                        this.$set(this.$data.product_info, n, val[n]);
                    }
                },
                deep: true
            },
        },
        validators: {
            'product_info.url': function(value) {
                return this.Validator.value(value).required('URL').url('httphttpsURL');
            },
            'product_info.title': function(value) {
                return this.Validator.value(value).required('');
            },
            'product_info.store_name': function(value) {
                return this.Validator.value(value).required('');
            },
            'product_info.price.mobile': function (price) {
                if($.inArray('mobile', this.root.form_data.base_type.flow_type) !== -1) {
                    return this.Validator.value(price).required('').regex('^([1-9][0-9]*)+(.[0-9]{1,2})?$','2').greaterThan(0,'0');
                }
            },
            'product_info.price.pc': function (price) {
                if($.inArray('pc', this.root.form_data.base_type.flow_type) !== -1) {
                    return this.Validator.value(price).required('PCPC').regex('^([1-9][0-9]*)+(.[0-9]{1,2})?$','2').greaterThan(0,'0');
                }
            },
        },
        methods: {
            validate: function() {
                this.$validate().then(function(success) {
                        if (!success) {
                            this.root.form_data.is_validate = false;
                            this.root.logs.errors.push(this.validation.errors);
                        }
                    }.bind(this));
            },
            validateReset: function () {
                this.validation.reset();
            },
            _inArray: function (val, arr) {
                return $.inArray(val, arr) !== -1
            },
            getProductInfo: function (showMsg) {
                if(this.validation.isPassed('product_info.url')) {
                    //
                    this.product_info.alternative_images = [];
                    this.product_info.images = [];

                    let _self = this;
                    let post_data = {
                        platform: this.root.form_data.base_type.platform,
                        url: this.product_info.url,
                        _token: $('meta[name="csrf-token"]').attr('content'),
                    };
                    let l = $( '.loadding-product-info-by-ajax' ).ladda();
                    l.ladda( 'start' );
                    $.ajax({
                        url: '/get_ecommerce_product_info',
                        type: 'post',
                        datatype: 'json',
                        data: post_data,
                        success: function (res) {
                            if(res.result === true) {
                                _self.product_info.title = res.title;
                                _self.product_info.store_name = res.store_name;
                                _self.product_info.price.pc = res.price.pc;
                                _self.product_info.price.mobile = res.price.mobile;
                                for(let i = 0; i < res.cover.length; ++i) {
                                    _self.product_info.alternative_images.push({
                                        title: '',
                                        value: res.cover[i],
                                        img: res.cover[i],
                                    });
                                }
                                new PNotify({
                                    title: '',
                                    text: '',
                                    type: 'success',
                                });
                            } else {
                                new PNotify({
                                    title: '',
                                    text: res.message ? res.message : '',
                                    type: 'info',
                                });
                            }
                        },
                        error: function () {
                            if(showMsg === true) {
                                new PNotify({
                                    title: '',
                                    text: '',
                                    type: 'error',
                                });
                            }
                        },
                        complete: function () {
                            setTimeout(function () {
                                l.ladda( 'stop' );
                            },200)
                        }
                    });
                } else {
                    if(showMsg === true) {
                        new PNotify({
                            title: '',
                            text: 'URLURL',
                            type: 'error',
                        });
                    }
                }

            }
        }
    }
</script>


